.about {
    min-height: 787px;
    background-color: #000;
    color: #FFF;
 
    &__text {
       width: 40rem;
       h4 {
          color: #6D95FC;
          font-size: 21px;
          font-weight: 500;
          letter-spacing: 3.045px;
          text-transform: uppercase;
          margin-bottom: 43px;
       }
 
       h1 {

          font-size: 48px;
          font-weight: 700;
          letter-spacing: 1.2px;
          margin-bottom: 43px;
 
          span {
             color: #6D95FC;
          }
       }
 
       p {
          color: #FFF;        
          font-size: 21px;
          font-weight: 300;
          margin-bottom: 43px;
          margin-top: 86px;
       }
 
       button {
          border-radius: 2px;
          border: 3px solid #6D95FC;
          padding: 10px 25px;
          text-align: center;
          font-size: 17px;
          font-weight: 400;
          letter-spacing: 2.465px;
          text-transform: uppercase;

          &:hover {
            background-color: #6D95FC;
            color: #000;
          }

          transition: all 1000ms;
       }
    }
 }
 .about__container {
    max-width: 1920px;
    min-height: 892px;
    background-image: url('../../../public/img/about-img.png');
    background-repeat: no-repeat;
    background-size: contain;
 }
 
 .line::after {
 content: '';
 position: absolute;
 width: 143px;
 height: 3px;
 background-color: #6D95FC;
 }